$btnColor: rgba(255, 211, 97, 1);

.btn {
  // background-color: Transparent;
  border: none;
  color: $btnColor;
  // padding: 15px 0px;
  font-size: 20px;
  font-weight: 600;
  // text-align:left;
  opacity: 50%;
}

.btn:hover {
  opacity: 100%;
}

.btn--primary {
  background-color: Transparent;
  border: none;
  text-align: left;
}

.btn--secondary {
  background-color: Transparent;
  padding: 30px 70px;
  color: $btnColor;
  font-size: 15px;
  font-weight: 600;
  align-items: center;
  text-align: center;
  opacity: 50%;
  letter-spacing: 0.1px;
  border: 1px solid rgba(127, 105, 48, 0);
}

.btn--secondary:hover {
  background: rgba(127, 105, 48, 0.25);
  border: 1px solid rgba(127, 105, 48, 0.25);
  opacity: 50%;
}

.btn--secondary:focus {
  background: rgba(127, 105, 48, 0.5);
}

@media screen and (max-width: 658px) {
  .btn--secondary {
    // padding: 30px 10px;
  }
}
