.bg1 {
  background-image: url("../../icons/BG.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  color: white;
}

.home {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 10%;
}

.wrapperCenter {
  padding: 0;
  margin: 0;
  height: 1000px;
  width: 75%;
}

.gameName {
  background-image: url("../../icons/GameName.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 76%;
  height: 35vh;
  width: 100%;
}

.arrow {
  padding-top: 25%;
  background-image: url("../../icons/arrow.png");
  background-position: bottom;
  background-repeat: no-repeat;
  height: 72px;
}

.container {
  padding: 1%;
  width: 30%;
  margin-left: 5%;
  background: linear-gradient(73.05deg, rgba(5, 5, 5, 0.2) 0%, rgba(5, 5, 5, 0) 53.53%);
  -webkit-backdrop-filter: blur(55px);
          backdrop-filter: blur(55px);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 200px;
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 10px 10px 10px 30px;
}

p {
  color: white;
  font-size: 22px;
  font-family: "Podkova", serif;
  opacity: 50%;
  margin-bottom: 5%;
}

.bg2-container {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 200px;
      grid-template-columns: 200px;
}

@media screen and (max-width: 1100px) {
  .container {
    width: 40%;
  }
  .gameName {
    background-image: url("../../icons/GameName.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 525px;
    height: 35vh;
    width: 100%;
  }
}
